<template>
  <div class="sort">
    <el-dialog
      title="添加"
      :visible="addStatus"
      width="600px"
      @close="close"
    >
      <el-form
        ref="from"
        label-position="right"
      >
        <el-form-item label="开始时间">
          <el-time-select
            v-model="form.startTime"
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '20:30'
            }"
            placeholder="请选择开始时间"
          />
        </el-form-item>
        <el-form-item label="结束时间">
          <el-time-select
            v-model="form.endTime"
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '20:30'
            }"
            placeholder="请选择结束时间"
          />
        </el-form-item>
        <el-form-item label="排序">
          <div style="width:300px">
            <el-input v-model="form.sort" />
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <span
          class="save"
          @click="addGoTime "
        >保存</span>
      </span>
    </el-dialog>
    <el-dialog
      title="修改"
      :visible="modifyStatus"
      width="600px"
      @close="close"
    >
      <el-form
        ref="from"
        label-width="100px"
      >
        <el-form-item label="开始时间">
          <el-time-select
            v-model="currentItem.start_time"
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30'
            }"
            :placeholder="currentItem.start_time "
          />
        </el-form-item>
        <el-form-item label="结束时间">
          <el-time-select
            v-model="currentItem.end_time"
            :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30'
            }"
            :placeholder="currentItem.end_time"
          />
        </el-form-item>
        <el-form-item label="排序">
          <el-input v-model="currentItem.sort" />
        </el-form-item>
      </el-form>
      <span slot="footer">
        <span
          class="save"
          @click="modifyGoTime "
        >保存</span>
      </span>
    </el-dialog>
    <div
      class="add"
      @click="add "
    >
      添加
    </div>
    <div class="sort-content">
      <el-table
        :data="sortData.list"
        border
        style="width=100%"
        :header-cell-style="{'text-align':'center','color':'black'}"
        :cell-style="{'text-align':'center','padding':'10px 0'}"
      >
        <el-table-column
          prop="id"
          label="序号"
        />
        <el-table-column
          prop="start_time"
          label="开始时间"
        />
        <el-table-column
          prop="end_time"
          label="结束时间"
        />
        <el-table-column
          prop="sort"
          label="排序"
        />
        <el-table-column
          label="状态"
          prop="desStatus"
        />
        <el-table-column
          prop="created_at"
          label="创建时间"
        />
        <el-table-column
          prop="updated_at"
          label="更新时间"
        />
        <el-table-column width="400px">
          <template
            slot="header"
            :slot-scope="scope"
          >
            操作
          </template>
          <template 
            
            slot-scope="scope"
          >
            <el-button
              size="mini"
              @click="modify(scope.row.id)"
            >
              修改
            </el-button>
            <el-switch
              style="margin-left:30px"
              v-model="scope.row.status"
              active-text="启用"
              inactive-text="禁用"
              @change="isUse(scope.row.id)"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination 
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="sortData.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click=" next"
      />
    </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            scope:"scope",
            position:"right",
            addStatus:false,
            modifyStatus:false,
            // 每页显示的条目数
            limit:15,
            // 获取哪一页数据
            pages:1,
            form:{
                startTime:"",
                endTime:"",
                sort:"",

            },
            // 更改时间段条目数据的id
            qucklyTimeId:"",
            sortData:[],
            currentItem:{}

        };
    },
    created() {
        this.getSortData();
    },
    methods: {
        // 获取分类列表
        async getSortData (){
            try {
                let  res = await this.axios.get("/goods/config/per/list",{
                        params:{
                        // 每页 15条
                            "limit":this.limit,
                            "pages":this.pages,
                            // 抢购
                            "type":2
                       
                        }
                    }),
                    
                    _data = res.data.list.map(item => {
                        return  Object.assign( item,{
                            status: item.status === 1 ? true : false,
                            desStatus: item.status === 1 ? "可用" : "禁用"
                        } );
                    });
                this.sortData =  Object.assign( res.data,{
                    list:_data
                });


                
            } catch (error) {
                console.log(error);
            }

        },
        // 添加抢购时间 
        async addGoTime (){
            if(!this.form.startTime && !this.form.endTime && !this.form.sort){
                this.$message({
                    message:"请添加时间和排序",
                    type:"warning"
                });
                return;

            }
            try {
                let res = await this.axios.post("/goods/config/per/add",{
                    "startTime": this.form.startTime,
                    "endTime" :this.form.endTime,
                    sort:this.form.sort
                });
                this.close();
                this.getSortData();
                this.$message({
                    message:res.msg,
                    type:"success"
                });
             
            } catch (error) {
                console.log(error);
            }


        },
        // 更改抢购时间状态
        async isUse (id){
            try {
                await this.axios.post("/goods/config/per/status",{
                    id
                });
                this.getSortData();
             
            } catch (error) {
                console.log(error);
            }

        },
        // 上一页
        prev (){
            this.pages--;
            this.getSortData();

        },
        // 翻页
        changePage (page){
            this.pages = page;
            this.getSortData();

        },
        // 下一页
        next(){
            this.pages++;
            this.getSortData();

        },
        // 添加
        add (){
            this.addStatus = true;
            
        },
        // 修改
        modify (id){
            this.modifyStatus = true;
            let currentItem = this.sortData.list.find(item => item.id === id);
            this.currentItem = Object.assign({},currentItem);
        },
        // 修改
        async modifyGoTime (){
            if(!this.currentItem.start_time && !this.currentItem.end_time  && !this.currentItem.sort){
                this.$message({
                    message:"请添加时间和排序",
                    type:"warning"
                });
                return;

            }
            try {
                let res = await this.axios.post("/goods/config/per/update",{
                    "startTime": this.currentItem.start_time,
                    "endTime":this.currentItem.end_time,
                    "sort":this.currentItem.sort,
                    "id":this.currentItem.id
                });
                this.close();
                this.getSortData();
                this.$message({
                    message:res.msg,
                    type:"success"
                });
             
            } catch (error) {
                console.log(error);
            }
          
        },
        close (){
            this.addStatus = false;
            this.modifyStatus = false;
            this.form = {};
        }
    },
    components: {

    }
};
</script>

<style lang="scss" scoped>
.sort{
    display: flex;
    flex-direction: column;
    .save{
        padding: 7px 30px;
        border-radius: 5px;
        color: white;
        cursor: pointer;
        background: #F7961C;
    }

    .add{
        color:#F7961C ;
        width: 95px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        border: 1px solid #F7961C;
        border-radius: 5px;
        align-self: flex-end;
        cursor: pointer;
    }
    .sort-content{
       margin: 25px 0 10px 0;
       height: 700px;
    }
}
</style>
