<template>
  <div class="qucklySort">
    <div class="sortTitle-container">
      <div
        v-for="(item,index) of sorts"
        :key="index"
        :class="['router',{'active':currentComponent === item.path }]"
        @click="currentComponent = item.path"
      >
        {{ item.name }}
      </div>
    </div>
    <div class="qucklySort-content">
      <component :is="currentComponent" />
    </div>
  </div>
</template>

<script>
import Sort from "./sort";
import Time from "./time";
import Beat from "./beat";
export default {
    data() {
        return {
          currentComponent:"Sort",
            sorts:[
                {
                    name:"分类设置",
                    path:"Sort",
                },
                {
                    name:"抢购时间设置",
                    path:"Time"
                },{
                    name:"折扣设置",
                    path:"Beat"
                }
            ]

        };
    },
    components: {
      Sort,
      Time,
      Beat

    }
};
</script>

<style lang="scss" scoped>
.qucklySort{
    .sortTitle-container{
        display: flex;
        .router{
            width: 125px;
            height: 40px;
            margin-bottom: 20px;
            cursor: pointer;
            color: #F7961C;
            display: grid;
            place-items: center;
        }
        .active{
            color: white;
            border-radius: 5px;
            background: #F7961C;
        }
    }

}
</style>
