<template>
  <div class="sort">
    <el-dialog
      title="添加"
      :visible.sync="showStatus"
      width="600px"
      @close="close"
    >
      <el-form
        ref="from"
        label-position="right"
      >
        <el-form-item label="类别名称">
          <div style="width:300px">
            <el-input v-model="form.kindName" />
          </div>
        </el-form-item>
        <el-form-item label="排序">
          <div style="width:300px">
            <el-input
              v-model="form.sort"
            />
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <span
          class="save"
          @click="addSell"
        >保存</span>
      </span>
    </el-dialog>
    <el-dialog
      title="修改"
      :visible="modifyStatus"
      width="600px"
      @close="close"
    >
      <el-form
        ref="from"
        label-position="right"
      >
        <el-form-item label="类别名称">
          <div style="width:300px">
            <el-input v-model="currentItem.name" />
          </div>
        </el-form-item>
        <el-form-item label="排序">
          <div style="width:300px">
            <el-input v-model="currentItem.sort" />
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <span
          class="save"
          @click="modifyGoTime "
        >保存</span>
      </span>
    </el-dialog>
    <div
      class="add"
      @click="add"
    >
      添加
    </div>
    <div class="sort-content">
      <el-table
        :data="sortData.list"
        border
        style="width=100%"
        :header-cell-style="{'text-align':'center','color':'black'}"
        :cell-style="{'text-align':'center','padding':'10px 0'}"
      >
        <el-table-column
          prop="id"
          label="序号"
        />
        <el-table-column
          prop="name"
          label="类别名称"
        />
        <el-table-column
          prop="sort"
          label="排序"
        />
        <el-table-column
          label="状态"
          prop="desStatus"
        />
        <el-table-column
          prop="created_at"
          label="创建时间"
        />
        <el-table-column
          prop="updated_at"
          label="更新时间"
        />
        <el-table-column width="400px">
          <template
            slot="header"
            :slot-scope="scope"
          >
            操作
          </template>
          <template 
            
            slot-scope="scope"
          >
            <el-button
              size="mini"
              @click="modify(scope.row.id)"
            >
              修改
            </el-button>
            <el-switch
              style="margin-left:30px"
              v-model="scope.row.status"
              active-text="启用"
              inactive-text="禁用"
              @change="isUse(scope.row.id)"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination 
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="sortData.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click=" next"
      />
    </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            scope:"scope",
            position:"right",
            showStatus:false,
            modifyStatus:false,
            // 每页显示的条目数
            limit:15,
            // 获取哪一页数据
            pages:1,
            form:{
                kindName:"",
                sort:"",

            },
            sortData:[],
            currentItem:{}

        };
    },
    created() {
        this.getSortData();
    },
    methods: {
        // 获取分类列表
        async getSortData (){
            try {
                let  res = await this.axios.get("/goods/config/dis/list",{
                        params:{
                        // 每页 15条
                            "limit":this.limit,
                            "pages":this.pages,
                       
                        }
                    }),
                    
                    _data = res.data.list.map(item => {
                        return  Object.assign( item,{
                            status: item.status === 1 ? true : false,
                            desStatus: item.status ? "可用" : "禁用"
                        } );
                    });
                this.sortData =  Object.assign( res.data,{
                    list:_data
                } );


                
            } catch (error) {
                console.log(error);
            }

        },
        // 更改折扣展示状态
        async isUse (id){
            try {
                await this.axios.post("/goods/config/dis/status",{
                    id
                });
                
                this.getSortData();
             
            } catch (error) {
                console.log(error);
            }

        },
        // 添加折扣
        async addSell (){
            if(!this.form.kindName && !this.form.sort){
                this.$message({
                    message:"请添加类别名称和排序",
                    type:"warning"
                });
                return;

            }
            try {
                let res = await this.axios.post("/goods/config/dis/add",{
                    name: this.form.kindName,
                    sort:this.form.sort
                });
                this.close();
                this.getSortData();
                this.$message({
                    message:res.msg,
                    type:"success"
                });
             
            } catch (error) {
                console.log(error);
            }

        },
        // 上一页
        prev (){
            this.pages--;
            this.getSortData();

        },
        // 翻页
        changePage (page){
            this.pages = page;
            this.getSortData();

        },
        // 下一页
        next(){
            this.pages++;
            this.getSortData();

        },
        // 添加
        add (){
            this.showStatus = true;
            
        },
        // 修改
        modify (id){
            this.modifyStatus = true;
            let currentItem = this.sortData.list.find(item => item.id === id);
            this.currentItem = Object.assign({},currentItem);
        },
        // 修改
        async modifyGoTime (){
            if(!this.currentItem.name && !this.currentItem.sort){
                this.$message({
                    message:"请添加类别名称和排序",
                    type:"warning"
                });
                return;

            }
            try {
                let res = await this.axios.post("/goods/config/dis/update",{
                    name: this.currentItem.name,
                    sort:this.currentItem.sort,
                    id:this.currentItem.id
                });
                this.close();
                this.getSortData();
                this.$message({
                    message:res.msg,
                    type:"success"
                });
             
            } catch (error) {
                console.log(error);
            }
          
        },
        close (){
            this.showStatus = false;
            this.modifyStatus = false;
            this.form = {};
        }
    },
    components: {

    }
};
</script>

<style lang="scss" scoped>
.sort{
    display: flex;
    flex-direction: column;
    .save{
        padding: 7px 30px;
        border-radius: 5px;
        color: white;
        cursor: pointer;
        background: #F7961C;
    }

    .add{
        color:#F7961C ;
        width: 95px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        border: 1px solid #F7961C;
        border-radius: 5px;
        align-self: flex-end;
        cursor: pointer;
    }
    .sort-content{
       margin: 25px 0 10px 0;
       height: 700px;
    }
}
</style>
