<template>
  <div class="sort">
    <el-dialog
      title="添加"
      :visible="addStatus"
      width="600px"
      @close="close"
    >
      <el-form
        label-position="right"
        ref="formContainer"
      >
        <el-form-item label="类别名称">
          <div style="width: 300px">
            <el-input v-model="form.kindName" />
          </div>
        </el-form-item>
        <el-form-item label="排序1">
          <div style="width: 300px">
            <el-input v-model="form.sort" />
          </div>
        </el-form-item>
        <el-form-item label="分类图片">
          <el-upload
            ref="upload"
            action
            :http-request="uploadImg"
            list-type="picture-card"
            :limit="1"
          >
            <i
              slot="default"
              class="el-icon-plus"
            />
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <span
          class="save"
          @click="save"
        >保存</span>
      </span>
    </el-dialog>
    <el-dialog
      title="修改"
      :visible="modifyStatus"
      width="600px"
      @close="close"
    >
      <el-form
        label-position="right"
        ref="formContainer"
      >
        <el-form-item label="类别名称">
          <div style="width: 300px">
            <el-input v-model="currentItem.name" />
          </div>
        </el-form-item>
        <el-form-item label="排序1">
          <div style="width: 300px">
            <el-input v-model="currentItem.sort" />
          </div>
        </el-form-item>
        <el-form-item class="upload-contianer" label="分类图片">
          <el-upload
            style="display:inline"
            ref="upload"
            action
            :http-request="uploadImg"
            list-type="picture-card"
            :limit="1"
          >
            <i
              slot="default"
              class="el-icon-plus"
            />
          </el-upload>
          <transition name="el-fade-in-linear">
            <img v-show="imgShow" class="icon-img" :src="currentItem.icon">
          </transition>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <span
          class="save"
          @click="modifyGoTime"
        >保存</span>
      </span>
    </el-dialog>
    <div
      class="add"
      @click="addStatus = true"
    >
      添加
    </div>
    <div class="sort-content">
      <el-table
        :data="sortData.list"
        border
        style="width=100%"
        :header-cell-style="{ 'text-align': 'center', color: 'black' }"
        :cell-style="{ 'text-align': 'center','padding':'5px 0' }"
      >
        <el-table-column
          prop="id"
          label="序号"
        />
        <el-table-column
          prop="name"
          label="类别名称"
        />
        <el-table-column label="图片">
          <template slot-scope="scope">
            <img
              style="width:50px;height:50px"
              :src="scope.row.icon"
            >
          </template>
        </el-table-column>
        <el-table-column
          prop="sort"
          label="排序"
        />
        <el-table-column
          label="状态"
          prop="desStatus"
        />
        <el-table-column
          prop="created_at"
          label="创建时间"
        />
        <el-table-column
          prop="updated_at"
          label="更新时间"
        />
        <el-table-column width="250px">
          <template
            slot="header"
            :slot-scope="scope"
          >
            操作
          </template>
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="modify(scope.row.id)"
            >
              修改
            </el-button>
            <el-switch
              style="margin-left: 30px"
              v-model="scope.row.status"
              active-text="启用"
              inactive-text="禁用"
              @change="isUse(scope.row.id)"
            />
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination
        layout="prev,pager,next"
        background
        :page-size="limit"
        :total="sortData.total"
        @current-change="changePage"
        @prev-click="prev"
        @next-click="next"
      />
    </div>
  </div>
</template>

<script>
import { client, setImgName } from "@/config/oss";
export default {
    data() {
        return {
            scope: "scope",
            position: "right",
            addStatus: false,
            modifyStatus: false,
            qucklyTimeId: "",
            // 每页显示的条目数
            limit: 9,
            // 获取哪一页数据
            pages: 1,
            form: {
                kindName: "",
                sort: "",
            },
            // 上传分类图片地址
            imgUrl: "",
            sortData: [],
            currentItem:{},
            imgShow:true,
        };
    },
    created() {
        this.getSortData();
    },
    methods: {
    // 获取分类列表
        async getSortData() {
            try {
                let res = await this.axios.get("/goods/config/nav/list", {
                    params: {
                        // 每页 15条
                        limit: this.limit,
                        pages: this.pages,
                        // 首页
                        type: 1,
                    },
                });

                this.sortData = Object.assign(res.data, {
                    list: res.data.list.map((item) => {
                        return Object.assign(item, {
                            status: item.status === 1 ? true : false,
                            desStatus: item.status === 1 ? "启用" : "禁用",
                        });
                    }),
                });
            } catch (error) {
                console.log(error);
            }
        },
        // 是否启用
        async isUse(id) {
            try {
                await this.axios.post("/goods/config/nav/status", {
                    id,
                });
                this.getSortData();
            } catch (error) {
                console.log(error);
            }
        },
        // 上一页
        prev() {
            this.pages--;
            this.getSortData();
        },
        // 翻页
        changePage(page) {
            this.pages = page;
            this.getSortData();
        },
        // 下一页
        next() {
            this.pages++;
            this.getSortData();
        },
        async save() {
            if (!this.form.kindName && !this.form.sort && !this.imgUrl) {
                this.$message({
                    message: "请添加完整信息",
                    type: "warning",
                });
                return;
            }
            let res = await this.axios.post("/goods/config/nav/add", {
                name: this.form.kindName,
                sort: this.form.sort,
                // 首页
                type: 1,
                icon: this.imgUrl,
            });
            this.close();
            this.getSortData();
            this.imgUrl = "";
            this.$message.success({
                message: res.msg,
            });
        },
        // 修改
        modify(id) {
            this.modifyStatus = true;
            let currentItem = this.sortData.list.find(item => item.id === id);
            this.currentItem = Object.assign({},currentItem);
            
        },
        // 修改
        async modifyGoTime() {
            if (!this.form.kindName && !this.form.sort && !this.imgUrl) {
                this.$message({
                    message: "请添加完整信息",
                    type: "warning",
                });
                return;
            }
            try {
                let res = await this.axios.post("/goods/config/nav/update", {
                    name: this.currentItem.name,
                    sort: this.currentItem.sort,
                    id: this.currentItem.id,
                    // 首页
                    type:1,
                    icon: this.imgUrl,
                });
                this.close();
                this.getSortData();
                this.imgUrl = "";
                this.$message({
                    message: res.msg,
                    type: "success",
                });
            } catch (error) {
                console.log(error);
            }
        },
        close() {
            this.form = {};
            this.$refs.upload.clearFiles();
            this.addStatus = false;
            this.modifyStatus = false;
        },
        // 添加分类图片
        async uploadImg(file) {
            let imgUrl = await setImgName(client(), file);
            this.imgUrl = imgUrl;
            this.imgShow = false;
        },
    },
    components: {},
};
</script>

<style lang="scss" scoped>
.sort {
  display: flex;
  flex-direction: column;
  .save {
    padding: 7px 30px;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    background: #f7961c;
  }

  .add {
    color: #f7961c;
    width: 95px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    border: 1px solid #f7961c;
    border-radius: 5px;
    align-self: flex-end;
    cursor: pointer;
  }
  .sort-content {
    margin: 25px 0 10px 0;
    height: 700px;
  }
}
.icon-img{
  width: 150px;
  height: 150px;
}
</style>
